@import "space";
@import "palette";

@mixin funcbox-comment($font-size-body) {
  .funcbox-textarea {
    overflow: hidden;
    outline: none;
    resize: none;
    color: $primary-text-color;
    background: $body-color;
    width: 100%;
    border: none;
    border-bottom: thin solid $divider-color;
  }
  .funcbox-textarea-error {
    margin-top: 5px;
    margin-bottom: 5px;
    color: $secondary-text-color;
    height: 30px;
    font-size: 16px;
    display: block;
  }

  .funcbox-textarea:focus {
    border-bottom: thin solid $primary-color;
  }

  .funcbox-textarea:hover {
  }


  .funcbox-textarea, .funcbox-button {
    display: block;
    font-size: $font-size-body;
  }
}



